import React, { useState } from "react";
import { products } from "../../mockData";
import { useStaticQuery, graphql } from "gatsby";
import { Link } from "gatsby";

const RandomProduct = () => {
  const data = useStaticQuery(graphql`
    query MyQuery {
      allFile {
        edges {
          node {
            publicURL
            sourceInstanceName
            relativePath
          }
        }
      }
    }
  `);
  const imgUrls = data.allFile.edges;

  let productImg = imgUrls.find((file) =>
    file.node.publicURL.endsWith(products?.outerImg)
  );

  const uniqueProducts = [];
  products.forEach((prod) => {
    if (!uniqueProducts.find((el) => el.categoryId === prod.categoryId)) {
      uniqueProducts.push(prod);
    }
  });
  const [randomNumber, setRandomNumber] = useState(
    Math.floor(Math.random() * 10)
  );
  return (
    <>
      <div className="home-product-grid">
        <Link to={`/products/${uniqueProducts[randomNumber + 1].id}`}>
          <div className="home-product-box green-bg">
            <div className="home-product-box-cover">
              <img
                src={
                  imgUrls.find((file) =>
                    file.node.publicURL.endsWith(
                      uniqueProducts[randomNumber + 1]?.outerImg
                    )
                  )?.node?.publicURL
                }
                alt=""
              />
            </div>
            <h4>{uniqueProducts[randomNumber + 1].name}</h4>
          </div>
        </Link>
        <Link to={`/products/${uniqueProducts[randomNumber + 2].id}`}>
          <div className="home-product-box blue-bg">
            <div className="home-product-box-cover">
              <img
                src={
                  imgUrls.find((file) =>
                    file.node.publicURL.endsWith(
                      uniqueProducts[randomNumber + 2]?.outerImg
                    )
                  )?.node?.publicURL
                }
                alt=""
              />
            </div>
            <h4>{uniqueProducts[randomNumber + 2].name}</h4>
          </div>
        </Link>
        <Link to={`/products/${uniqueProducts[randomNumber + 3].id}`}>
          <div className="home-product-box purple-bg">
            <div className="home-product-box-cover">
              <img
                src={
                  imgUrls.find((file) =>
                    file.node.publicURL.endsWith(
                      uniqueProducts[randomNumber + 3]?.outerImg
                    )
                  )?.node?.publicURL
                }
                alt=""
              />
            </div>
            <h4>{uniqueProducts[randomNumber + 3].name}</h4>
          </div>
        </Link>
        <Link to={`/products/${uniqueProducts[randomNumber + 4].id}`}>
          <div className="home-product-box cyan-bg">
            <div className="home-product-box-cover">
              <img
                src={
                  imgUrls.find((file) =>
                    file.node.publicURL.endsWith(
                      uniqueProducts[randomNumber + 4]?.outerImg
                    )
                  )?.node?.publicURL
                }
                alt=""
              />
            </div>
            <h4>{uniqueProducts[randomNumber + 4].name}</h4>
          </div>
        </Link>
      </div>
    </>
  );
};

export default RandomProduct;
